<template>
    <div @click="clickSelect"   class="customInputSelect">

    <div class="customInputSelectBG" :class="{active:isShow}"></div>
    <span>{{value}}</span>
        <div class="customInputSelectOptions" :class="{active:isShow}" >
            <input @click="clickInput" type="text"   />
            <ul @click="selectOption">
                <li v-for="(item) of option" :key="item.id" :attr-countryCode="item.optionValue.countryCode" :attr-localCurrency="item.optionValue.localCurrency"  >{{ item.optionName}}</li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
  props: ['value', 'option'],
  data () {
    return {
      isShow: false
    }
  },
  methods: {
    clickSelect () {
      this.isShow = !this.isShow
    },
    selectOption (event) {
      this.$emit('optionClick', {
        currentCountry: event.target.getAttribute('attr-countrycode'),
        localCurrency: event.target.getAttribute('attr-localCurrency'),
        paymentCurrency: event.target.getAttribute('attr-localCurrency'),
        payerType: 'PERSONAL',
        BeneficiaryType: 'PERSONAL',
        paymentMethod: 'LOCAL'
      })
    },
    clickInput (event) {
      event.stopPropagation()
    }
  }
}
</script>
<style scoped>
/*  customInputSelect Component */
.customInputSelect {
  width: 240px;
  height: 48px;
  border: 1px solid #2a2a2a;
  border-radius: 5px;
  line-height: 48px;
  cursor: pointer;
}
.customInputSelect span {
  font-size: 18px;
  font-weight: bold;
  padding-left: 10px;
}
.customInputSelectOptions {
  border: 1px solid #2a2a2a;
  border-radius: 5px;
  top: -49px;
  left: -1px;
  width: 242px;
  background-color: #fff;
  box-shadow: 2px 2px 5px #999;
  position: relative;
  display: none;
}
.customInputSelectOptions input {
  border: 0px;
  border-bottom: 1px solid #2a2a2a;
  padding-left: 10px;
  width: 232px;
  height: 44px;
  font-size: 18px;
}
.customInputSelectOptions li {
  border-bottom: 1px solid #2a2a2a;
  padding-left: 10px;
}
.customInputSelectBG {
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  display: none;
  cursor: default;
}
.active {
  display: block;
}
</style>



