<template>
<article>
<section class="container">
    <header>
        <div class="headerBG">
            <div class="headerLine1"></div>
            <div class="headerLine2"></div>
            <div class="headerLine3"></div>
            <div class="headerContaier">
                <div class="headerLogo"></div>
                <h1>{{language==="cn"?"支付指南":"Country payout guide"}}</h1>
            </div>
            <div class="section1Contaier" >
                <div class="">
                    <span class="buttonLine">{{language === "cn" ? "打印" : "Print"}}</span>
                    <span class="buttonLine" @click="changeLanguage">{{language === "cn" ? "中文" : "English"}}</span>
                </div>
                <div class="section2Contaier">
                  <div class="customSelectBG">
                    <span class="selectTitle">{{language === "cn" ? "国家" : "Country"}}</span>
                    <customInputSelect :value="localCountry" :option="countrys" @optionClick="optionClickHandle"  />
                  </div>
                  <div class="customSelectBG">
                    <span class="selectTitle"> {{language === "cn" ? "付款客户类型" : "Payer Type"}}</span>
                    <customSelect :value="payerTypeName" name="payerType" :option="payer" @optionClick="optionClickHandle" />
                  </div>
                  <div class="customSelectBG">
                    <span class="selectTitle"> {{language === "cn" ? "收款客户类型" : "Beneficiary Type"}}</span>
                    <customSelect :value="beneficiaryTypeName" name="BeneficiaryType" :option="beneficiary" @optionClick="optionClickHandle" />
                  </div>
                  <div class="customSelectBG">
                    <span class="selectTitle">{{language === "cn" ? "币种" : "Currency"}}</span>
                    <customSelect :value="localCurrency" name="paymentCurrency" :option="currencyArr" @optionClick="optionClickHandle" />
                  </div>
                  <div class="customSelectBG">
                    <span class="selectTitle">{{language === "cn" ? "支付方式" : "Payment Method"}}</span>
                    <div v-if="getPaymentmethod.length ===1" class="customSelectline">{{getPaymentmethod[0]}}</div>
                    <customSelect v-else :value="paymentMethod" name="paymentMethod" :option="paymentmethod" @optionClick="optionClickHandle" />
                  </div>
              </div>
            </div>
         </div>
    </header>
    <article class="payoutGuide">
      <section class="section3">
            <h2> {{language === "cn" ? "必填字段" : "Required Fields"}}</h2>
            <h3>{{language === "cn" ? "收款銀行信息" : "Beneficiary Bank Details"}}</h3>

            <table>
              <tbody>
                <tr v-for="(item) in beneficiaryBank"  :key="item.id" >
                  <td class="title">{{language === "cn"?item.name.cn:item.name.en}}</td>
                  <td class="value">{{language === "cn"?item.desc.cn:item.desc.en}}</td>
                </tr>

              </tbody>
            </table>
            <h3>{{language === "cn" ? "收款人信息" : "Beneficiary Details"}}</h3>
            <table>
              <tbody>
                <table>
              <tbody>
                <tr  v-for="(item) in beneficiaryDetails"  :key="item.id" >
                  <td class="title">{{language === "cn"?item.name.cn:item.name.en}}</td>
                  <td class="value">{{language === "cn"?item.desc.cn:item.desc.en}}</td>
                </tr>
              </tbody>
            </table>
              </tbody>
            </table>
            <h3>{{language === "cn" ? "付款人信息" : "Payer Details"}}</h3>
            <table>
              <tbody>
                <tr  v-for="(item) in payerDetails"  :key="item.id" >
                  <td class="title">{{language === "cn"?item.name.cn:item.name.en}}</td>
                  <td class="value">{{language === "cn"?item.desc.cn:item.desc.en}}</td>
                </tr>
              </tbody>
            </table>
            <h3>{{language === "cn" ? "付款信息" : "Payment Details"}}</h3>
            <table>
              <tbody>
                <tr  v-for="(item) in paymentDetails"  :key="item.id" >
                  <td class="title">{{language === "cn"?item.data.language.cn:item.data.language.en}}</td>
                  <td class="value">{{language === "cn"?item.data.desc.cn:item.data.language.en}}</td>
                </tr>
              </tbody>
            </table>
      </section>
      <section class="section4">
        <h2>Business constraints</h2>
        <h3>Supported Bank Channels</h3>
        <p>{{language==="cn"?supportedBankChannels.cn:supportedBankChannels.en}}</p>
        <h3>Transaction Limit</h3>
        <p>{{language==="cn"?transactionLimit.cn:transactionLimit.en}}</p>
        <h3>Maximum Reference Limit</h3>
        <p>{{language==="cn"?maximumReferenceLimit.cn:maximumReferenceLimit.en}}</p>
      </section>
    </article>
</section>
<section class="bgloading" :class="{show:isShow}"  > </section>
</article>
</template>
<script>
import customSelect from '../components/customSelect'
import customInputSelect from '../components/customInputSelect'
import countrys from '../store/country'
export default {
  computed: {
    language () {
      return this.$store.state.language
    },
    currentCountry () {
      return this.$store.state.currentCountry
    },
    localCurrency () {
      return this.$store.state.localCurrency
    },
    payerType () {
      return this.$store.state.payerType
    },
    BeneficiaryType () {
      return this.$store.state.BeneficiaryType
    },
    paymentMethod () {
      return this.$store.state.paymentMethod
    },
    paymentCurrency () {
      return this.$store.state.paymentCurrency
    },
    beneficiaryBank () {
      return this.$store.state.beneficiaryBanklsRequired
    },
    beneficiaryDetails () {
      return this.$store.state.beneficiaryDetailsRequired
    },
    payerDetails () {
      return this.$store.state.payerDetailsRequired
    },
    paymentDetails () {
      console.log(this.$store.state.paymentDetails)
      return this.$store.state.paymentDetails
    },

    countrys () {
      return this.$store.state.country.map(item => {
        return {
          optionName: this.language === 'cn' ? item.countryName.cn : item.countryName.en,
          optionValue: {
            localCurrency: item.localCurrency,
            countryCode: item.countryCode
          }
        }
      })
    },
    localCountry () {
      return this.language === 'cn' ? countrys[this.currentCountry].countryName.cn : countrys[this.currentCountry].countryName.en
    },
    payer () {
      return [{ name: 'PERSON', value: 'PERSONAL' }, { name: 'BUSINESS', value: 'COMPANY' }]
    },
    beneficiary () {
      return [{ name: 'PERSON', value: 'PERSONAL' }, { name: 'BUSINESS', value: 'COMPANY' }]
    },
    payerTypeName () {
      return this.payerType === 'PERSONAL' ? 'PERSON' : 'BUSINESS'
    },
    beneficiaryTypeName () {
      return this.BeneficiaryType === 'PERSONAL' ? 'PERSON' : 'BUSINESS'
    },
    currencyArr () {
      return this.$store.state.currencyArr.map((item) => {
        return { name: item, value: item }
      })
    },
    paymentmethod () {
      return [{ name: 'LOCAL', value: 'LOCAL' }, { name: 'SWIFT', value: 'SWIFT' }]
    },
    getPaymentmethod () {
      if (!this.$store.state.orderDetails.payment_method) { return false };
      return this.$store.state.orderDetails.payment_method
    },
    supportedBankChannels () {
      let supportedbankchannels
      switch (this.currentCountry) {
        case 'CN':
          if (this.payerType === 'PERSONAL' && this.BeneficiaryType === 'PERSONAL') {
            supportedbankchannels = {
              en: 'Supported banks',
              cn: '支持的银行渠道'
            }
          } else if (this.payerType === 'PERSONAL' && this.BeneficiaryType === 'COMPANY') {
            supportedbankchannels = {
              en: 'Supported banks',
              cn: '支持的银行渠道'
            }
          } else {
            supportedbankchannels = {
              en: 'All bank channels',
              cn: '所有银行渠道'
            }
          }
          break
        case 'IN':
          supportedbankchannels = {
            en: 'Supported banks',
            cn: '支持的银行渠道'
          }
          break
        case 'PH':
          supportedbankchannels = {
            en: 'Supported banks',
            cn: '支持的银行渠道'
          }
          break
        case 'MY':
          supportedbankchannels = {
            en: 'Supported banks',
            cn: '支持的银行渠道'
          }
          break
        case 'TH':
          supportedbankchannels = {
            en: 'Supported banks',
            cn: '支持的银行渠道'
          }
          break
        case 'VN':
          supportedbankchannels = {
            en: 'Supported banks',
            cn: '支持的银行渠道'
          }
          break
        default:
          supportedbankchannels = {
            en: 'All bank channels',
            cn: '所有银行渠道'
          }
          break
      }

      return supportedbankchannels
    },
    // TransactionLimit 交易限额
    transactionLimit () {
      let method = this.getPaymentmethod
      let transactionLimit
      let transactionLimitStatus = false
      switch (this.paymentCurrency) {
        case 'USD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            transactionLimit = {
              en: '75,000 USD per transaction',
              cn: '每笔75,000 USD'
            }
          } else {
            transactionLimitStatus = true
          }
          break
        case 'CAD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            transactionLimit = {
              en: '20,000 USD per transaciton',
              cn: '每笔20,000 USD'
            }
          } else {
            transactionLimitStatus = true
          }
          break
        case 'GBP':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            transactionLimit = {
              en: '250,000 USD per transcation',
              cn: '每笔25,000 USD'
            }
          } else {
            transactionLimitStatus = true
          }
          break
        default:
          transactionLimit = {
            en: 'N/A',
            cn: 'N/A'
          }
          break
      }
      if (transactionLimitStatus) {
        transactionLimit = {
          en: 'N/A',
          cn: 'N/A'
        }
      }

      return transactionLimit
    },
    // reference交易附言
    maximumReferenceLimit () {
      let reference
      let referenceState = false
      let method = this.getPaymentmethod
      switch (this.paymentCurrency) {
        case 'AUD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = { en: 'Maximum 18 characters', cn: '最长18位英文字母或阿拉伯数字组合' }
          } else {
            referenceState = true
          }
          break
        case 'CNY':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = { en: 'Maximum 60 characters', cn: '最长60位英文字母或阿拉伯数字组合' }
          } else {
            referenceState = true
          }
          break
        case 'USD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = {
              en: 'Maximum 10 characters',
              cn: '最长10位英文字母或阿拉伯数字组合'
            }
          } else {
            referenceState = true
          }
          break
        case 'CAD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = {
              en: 'Maximum 30 characters ',
              cn: '最长30位英文字母或阿拉伯数字组合'

            }
          } else {
            referenceState = true
          }
          break
        case 'GBP':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = {
              en: 'Maximum 16 characters',
              cn: '最长16位英文字母或阿拉伯数字组合'
            }
          } else {
            referenceState = true
          }
          break
        case 'EUR':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = {
              en: 'Maximum 35 characters',
              cn: '最长35位英文字母或阿拉伯数字组合'
            }
          } else {
            referenceState = true
          }
          break

        case 'HKD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = {
              en: 'Maximum 12 characters',
              cn: '最长12位英文字母或阿拉伯数字组合'
            }
          } else {
            referenceState = true
          }
          break
        case 'SGD':
          if (this.paymentMethod === 'LOCAL' && method.length === 2) {
            reference = {
              en: 'Maximum 16 characters',
              cn: '最长16位英文字母或阿拉伯数字组合'
            }
          } else {
            referenceState = true
          }
          break
        default:
          reference = {
            en: 'Maximum 140 characters (subject to be modified due to requirements of receiving banks, please keep it as brief as possible)',
            cn: '最长140位英文字母或阿拉伯数字组合（可能会被中间行删改，请尽量保持简短）'
          }
          break
      }
      if (referenceState) {
        reference = {
          en: 'Maximum 140 characters (subject to be modified due to requirements of receiving banks, please keep it as brief as possible)',
          cn: '最长140位英文字母或阿拉伯数字组合（可能会被中间行删改，请尽量保持简短）'
        }
      }

      return reference
    },
    isShow () {
      return this.$store.state.isShow
    }
  },
  created () {
    this.$store.dispatch('getFullSecham', {
      countryCode: this.currentCountry,
      localCurrency: this.localCurrency,
      payerType: this.payerType,
      BeneficiaryType: this.BeneficiaryType,
      paymentMethod: this.paymentMethod,
      paymentCurrency: this.paymentCurrency
    })
  },
  methods: {
    changeLanguage () {
      if (this.language === 'cn') {
        this.$store.commit('changeState', { key: 'language', value: 'en' })
      } else {
        this.$store.commit('changeState', { key: 'language', value: 'cn' })
      }
    },
    optionClickHandle (data) {
      let keys = Object.keys(data)
      for (let item of keys) {
        this.$store.commit('changeState', { key: item, value: data[item] })
      }
      this.$store.dispatch('getFullSecham', {
        countryCode: this.currentCountry,
        localCurrency: this.localCurrency,
        payerType: this.payerType,
        BeneficiaryType: this.BeneficiaryType,
        paymentMethod: this.paymentMethod,
        paymentCurrency: this.paymentCurrency
      })
      this.$store.commit('changeState', { key: 'localCurrency', value: this.paymentCurrency })
    }

  },
  components: {
    customSelect,
    customInputSelect
  }
}
</script>
<style scoped>
.headerBG {
  position: relative;
}
.headerLine1 {
  background: url("../assets/Header-Line.png") no-repeat left top;
  position: absolute;
  left: 0px;
  top: 0px;
  height: 626px;
  width: 100%;
  z-index: 1;
}
.headerLine2 {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  background-color: #030303;
  height: 248px;
}
.headerLine3 {
  position: absolute;
  left: 0px;
  top: 248px;
  width: 100%;
  background-color: #eaecf0;
  height: 280px;
  z-index: 2;
}
.headerLogo {
  background: url("../assets/logo.png") #000 left 8px no-repeat;
  width: 175px;
  height: 40px;
}
.headerContaier {
  padding-top: 16px;
  width: 1052px;
  color: #fff;
  font-size: 26px;
  height: 232px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}
.headerContaier h1 {
  font-size: 56px;
  padding-top: 80px;
}
.section1Contaier {
  padding-top: 40px;
  height: 240px;
  width: 1052px;
  margin: 0 auto;
  position: relative;

  background-color: #eaecf0;
  z-index: 2;
}
.section1Contaier .buttonLine {
  width: 108px;
  height: 48px;
  display: inline-block;
  background-color: #000;
  border-radius: 20px;
  color: #fff;
  text-align: center;
  line-height: 48px;
  font-size: 18px;
  cursor: pointer;
  margin-right: 20px;
}
.section2Contaier {
  margin-top: 56px;
  display: flex;
}
.customSelectBG {
  margin-right: 15px;
}
.customSelectline {
  height: 48px;
  padding-left: 10px;
  line-height: 48px;
  font-size: 18px;
}
.selectTitle {
  font-size: 24px;
  color: #54545e;
  display: block;
  font-weight: 500;
  padding-bottom: 16px;
}

.payoutGuide {
  display: flex;
  padding-top: 52px;
  width: 1052px;

  margin: 0px auto;
}
.section3 {
  width: 590px;
}
.section3 h2 {
  font-size: 40px;
  color: #612fff;
  font-weight: 500;
}
.section3 h3 {
  font-size: 20px;
  color: #612fff;
  font-weight: bold;
  padding-top: 30px;
  padding-bottom: 8px;
  border-bottom: 1px solid #2a2a2a;
}
.section3 table tr {
  height: 60px;
}
.section3 table .title {
  font-size: 20px;
  width: 262px;
  color: #080f2b;
  font-weight: bold;
}
.section3 table .value {
  font-size: 20px;
  color: #54545e;
}
.section4 {
  width: 400px;
  margin-left: 66px;
}
.section4 p {
  padding-top: 15px;
}
.section4 h2 {
  font-size: 40px;
  color: #ff4f42;
  font-weight: 500;
}
.section4 h3 {
  font-size: 20px;
  color: #ff4f42;
  font-weight: bold;
  padding-top: 30px;
  padding-bottom: 8px;
  border-bottom: 1px solid #2a2a2a;
}
.bgloading {
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  background: url("../assets/loading.png") no-repeat center center #fff;
  opacity: 0.7;
  z-index: 1000;
  display: none;
}

.show {
  display: block;
}
</style>



