<template>
    <div @click="clickSelect"   class="customSelect">

    <div class="customSelectBG" :class="{active:isShow}"></div>
        <span>{{value}}</span>
        <div  class="customSelectOptions" :class="{active:isShow}" >
            <ul  @click="selectOption">
                <li v-for="(item) of option" :key="item.id" :attr-value="item.value"  >{{item.name}}</li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
  props: ['value', 'option', 'name'],
  data () {
    return {
      isShow: false
    }
  },
  methods: {
    clickSelect () {
      this.isShow = !this.isShow
    },
    selectOption (event) {
      let data = {}
      data[this.$props.name] = event.target.getAttribute('attr-value')
      this.$emit('optionClick', data)
    }
  }
}
</script>
<style scoped>
/*  customSelect Component */
.customSelect {
  width: 200px;
  height: 48px;
  border: 1px solid #2a2a2a;
  border-radius: 5px;
  line-height: 48px;
  cursor: pointer;
}
.customSelect span {
  font-size: 18px;
  font-weight: bold;
  padding-left: 10px;
}
.customSelectOptions {
  border: 1px solid #2a2a2a;
  border-radius: 5px;
  top: -49px;
  left: -1px;
  width: 152px;
  background-color: #fff;
  box-shadow: 2px 2px 5px #999;
  position: relative;
  display: none;
}

.customSelectOptions li {
  border-bottom: 1px solid #2a2a2a;
  padding-left: 10px;
}
.customSelectBG {
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  display: none;
  cursor: default;
}
.active {
  display: block;
}
</style>



