import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'queryString'
import dataArr from './data'
import countrys from './country'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    language: 'cn',
    // isPDFState: false,
    currentCountry: 'AU',
    localCurrency: 'AUD',
    payerType: 'PERSONAL',
    BeneficiaryType: 'PERSONAL',
    paymentMethod: 'LOCAL',
    paymentCurrency: 'AUD',
    // 收款银行信息
    beneficiaryBank: {},
    // 收款客户信息
    beneficiaryDetails: {},
    // 付款客户信息
    payerDetails: {},
    // 支付信息
    paymentDetails: {},
    // 其他信息
    orderDetails: {},
    country: [],
    currencyArr: [],
    isShow: true,
    beneficiaryBanklsRequired: [],
    beneficiaryDetailsRequired: [],
    payerDetailsRequired: []
  },
  mutations: {
    reloadData (state, payload) {
      let data = payload.data
      state.beneficiaryBank = {}
      state.beneficiaryDetails = {}
      state.payerDetails = {}
      state.paymentDetails = {}
      state.orderDetails = {}
      state.country = []
      state.currencyArr = []
      state.beneficiaryBanklsRequired = []
      state.beneficiaryDetailsRequired = []
      state.payerDetailsRequired = []
      for (let item in data) {
        if (item.indexOf('beneficiary.') !== -1) {
          // 判断BeneficiaryBank
          if (item.indexOf('beneficiary.bank_details.') !== -1) {
            if (dataArr[item]) {
              if (item === 'beneficiary.bank_details.account_routing_type1') {
                let routingType = data[item].validation_functions[0].args[0]
                if (routingType) {
                  Vue.set(state.beneficiaryBank, item, {
                    data: dataArr[item][routingType],
                    ...data[item]
                  })
                }
              } else {
                Vue.set(state.beneficiaryBank, item, {
                  data: dataArr[item],
                  ...data[item]
                })
              }
            }
          } else {
            if (dataArr[item]) {
              Vue.set(state.beneficiaryDetails, item, {
                data: dataArr[item],
                ...data[item]
              })
            }
          }
        } else if (item.indexOf('payer.') !== -1) {
          if (dataArr[item]) {
            Vue.set(state.payerDetails, item, {
              data: dataArr[item],
              ...data[item]
            })
          }
        } else if (item.indexOf('reason') !== -1) {
          Vue.set(state.paymentDetails, 'reason', {
            data: dataArr['reason'],
            ...data[item]
          })
        } else if (item.indexOf('reference') !== -1) {
          Vue.set(state.paymentDetails, 'reference', {
            data: dataArr['reference'],
            ...data[item]
          })
        } else {
          if (item.indexOf('payment_method') !== -1) {
            let method = StirngtoArray(data[item].func_desc, 'OneOf([', '])')
            if (method.length === 1) {
              Vue.set(state.orderDetails, item, StirngtoArray(data[item].func_desc, 'Equal("', '")'))
            } else {
              Vue.set(state.orderDetails, item, method)
            }
          } else {
            Vue.set(state.orderDetails, item, data[item])
          }
        }
      }
      if (state.orderDetails.payment_method.length === 1 && state.paymentMethod !== state.orderDetails.payment_method[0]) {
        this.commit('changeState', { key: 'paymentMethod', value: state.orderDetails.payment_method[0] })
        this.dispatch('getFullSecham', {
          countryCode: state.currentCountry,
          localCurrency: state.localCurrency,
          payerType: state.payerType,
          BeneficiaryType: state.BeneficiaryType,
          paymentMethod: state.orderDetails.payment_method[0],
          paymentCurrency: state.paymentCurrency
        })
        return
      }

      let country = StirngtoArray(state.beneficiaryBank['beneficiary.bank_details.bank_country_code'].func_desc, 'OneOf([', '])')
      for (let item of country) {
        let countryitem = countrys[item]
        if (countryitem) {
          state.country.push({
            countryCode: item,
            ...countryitem
          })
        }
      }

      let currencys = StirngtoArray(state.orderDetails['payment_currency'].func_desc, 'OneOf([', '])')
      for (let item of currencys) {
        if (item !== 'IDR') {
          state.currencyArr.push(item)
        }
      }
      this.commit('changeData')
      this.commit('changeShow', false)
    },
    changeData (state) {
      // 特殊字段处理
      //   Object.keys(state.beneficiaryBank).reduce((from, key) => {
      //     beneficiaryBank[key] = state.beneficiaryBank[key]
      //     return beneficiaryBank
      //   })
      for (let key in state.beneficiaryBank) {
        let payloadKey = state.beneficiaryBank[key]
        let name = payloadKey.data.language
        let desc = payloadKey.data.desc
        if (state.currentCountry === 'CN' && key === 'beneficiary.bank_details.account_name') {
          desc = {
            cn: '收款客户的开户名',
            en: 'Account name of the beneficiary in Chinese'
          }
        }
        if (state.currentCountry === 'US' && state.paymentMethod === 'SWIFT' && key === 'beneficiary.bank_details.account_routing_type1') {
          name = {
            cn: 'ABA - WIRE TRANSFER',
            en: 'ABA - WIRE TRANSFER'
          }
          desc = {
            cn: '美国FedWire清算网络的9位数字银行识别码',
            en: ' A 9-digit bank routing ID for members of FedWire electronic funds transfer system'
          }
        }
        if (state.currentCountry === 'SG' && key === 'beneficiary.bank_details.account_routing_type1') {
          name = {
            cn: 'BANK CODE',
            en: 'BANK CODE'
          }
          desc = {
            cn: '新加坡本地清算网络的7位数字银行识别码',
            en: 'A 7-digit bank routing ID for members of Singapore local clearing system'
          }
        }
        state.beneficiaryBanklsRequired.push({
          name: name,
          desc: desc
        })
      }

      // 去除不必要字段
      for (let key in state.beneficiaryDetails) {
        let payloadKey = state.beneficiaryDetails[key]
        let name = payloadKey.data.language
        let desc = payloadKey.data.desc
        if (state.BeneficiaryType === 'PERSONAL') {
          // 判断翻译的字段最为精准
          if (state.language === 'cn') {
            if (name['cn'].indexOf('企业') === -1) {
              if (payloadKey.required) {
                state.beneficiaryDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          } else {
            if (name['en'].indexOf('Business') === -1) {
              if (payloadKey.required) {
                state.beneficiaryDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          }
        } else {
          if (state.language === 'cn') {
            if (name['cn'].indexOf('个人') === -1) {
              if (payloadKey.required) {
                state.beneficiaryDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          } else {
            if (name['en'].indexOf('Person') === -1) {
              if (payloadKey.required) {
                state.beneficiaryDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          }
        }
      }
      for (let key in state.payerDetails) {
        let payloadKey = state.payerDetails[key]
        let name = payloadKey.data.language
        let desc = payloadKey.data.desc
        if (state.BeneficiaryType === 'PERSONAL') {
          // 判断翻译的字段最为精准
          if (state.language === 'cn') {
            if (name['cn'].indexOf('企业') === -1) {
              if (payloadKey.required) {
                state.payerDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          } else {
            if (name['en'].indexOf('Business') === -1) {
              if (payloadKey.required) {
                state.payerDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          }
        } else {
          if (state.language === 'cn') {
            if (name['cn'].indexOf('个人') === -1) {
              if (payloadKey.required) {
                state.payerDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          } else {
            if (name['en'].indexOf('Person') === -1) {
              if (payloadKey.required) {
                state.payerDetailsRequired.push({
                  name: name,
                  desc: desc
                })
              }
            };
          }
        }
      }
      console.log(state.payerDetailsRequired)
    },
    changeShow (state, boolean) {
      state.isShow = boolean
    },
    changeState (state, obj) {
      state[obj.key] = obj.value
    }
  },

  actions: {
    getFullSecham (context, param) {
      context.commit('changeShow', true)
      axios({
        method: 'POST',
        url: '/getFullSchema',
        headers: {
          'content-type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify(param)
      }).then((res) => {
        context.commit({ type: 'reloadData', data: res.data })
      }).catch((error) => {
        console.log(error)
      })
    }

  }
})

function StirngtoArray (str, leftrexp, rightrexp) {
  let arr = str.split(', ')
  arr[0] = arr[0].replace(leftrexp, '')
  arr[arr.length - 1] = arr[arr.length - 1].replace(rightrexp, '')
  return arr
}



